
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XDialog from 'vux/src/components/x-dialog/index.vue'


export default {
props: ['showDialog', 'dialogTitle'],
directives: {
TransferDom
},
components: {
XDialog
},
data () {
return {
}
},
created () {
this.contentHeight = window.innerHeight * 62 / 100
},
methods: {
toHidden () {
this.$emit('hidden')
}
}
}



