import Scroll from '@/components/scroll/scroll'

export default {
  props: {
    content: {
      type: String,
      default: ''
    },
    contentHTML: {
      type: String,
      default: ''
    }
  },
  components: {
    Scroll
  },
  methods: {
    scroll (position) {
      this.$emit('scroll', position)
    }
  }
}



