
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import Scroll from '@/components/scroll/scroll'
import Product from '@/pages/cart/cart-page/scenic-spot/productTab/productInfo'
import ProductDetail from '@/pages/cart/cart-page/scenic-spot/productTab/productDetail'
import productComment from '@/pages/cart/cart-page/scenic-spot/productTab/productComment'
import VHeader from '@/components/v-header/v-header'
import Share from '@/components/share/share'

import axios from '@/api/axiosApi'
import { localUser } from '@/assets/js/local'
import { APP_DOWNLOAD_PATH } from '@/api/config'
import { isIOS, isIphoneX } from '@/assets/js/brower'
import defaultImg from '@/assets/img/default_avatar.png'

export default {
name: 'Product1',
data () {
return {
APP_DOWNLOAD_PATH,
num: 0,
showShare: false,
shareObj: {},
opacity: 0,
paddingTop: 0,
productInfo: {
feature: '',
notice: '',
atlas: [{src: ''}]
},
orderDataList: [],
commentList: [],
recommendList: [],
collectionType: 0,
messageRecordId: ''
}
},
components: {
Tab,
TabItem,
XHeader,
Product,
ProductDetail,
Scroll,
productComment,
VHeader,
Share
},
created () {
this.paddingTop = 40
if (isIphoneX()) {
this.paddingTop = 34
} else {
this.paddingTop = 20
}
this.tabList = [this.$route.params.type === '0' ? '酒店' : '景点', '详情', '评价']
this._getProductInfo()
this._getOrderData()
this._getCommentData()
this._getSystemRecommend()
if (localUser.get('id')) {
this._browse()
this._getCollectionStatus()
}
},
watch: {
'$route' (to, from) {
if (to.name === 'Product') {
this.tabList = [this.$route.params.type === '0' ? '酒店' : '景点', '详情', '评价']
this._getProductInfo()
this._getOrderData()
this._getCommentData()
this._getSystemRecommend()
if (localUser.get('id')) {
this._browse()
this._getCollectionStatus()
}
}
}
},
mounted () {
this.paddingTop = this.$refs.header.$el.offsetHeight
},
methods: {
_getProductInfo () {
axios.get(this, '/v1/product/' + this.$route.params.productId, null,
(res) => {
if (res.feature) {
res.feature = res.feature.split('；')
}
if (res.notice) {
res.notice = res.notice.split('；')
}
if (res.atlas) {
res.atlas = res.atlas.split(';')
let tmp = []
res.atlas.forEach((item, index) => {
tmp.push({
src: item
})
})
res.atlas = tmp
}
this.productInfo = res
this.shareObj = {
productName: res.name,
productDesc: res.desc,
thumbUrl: res.atlas.length > 0 ? res.atlas[0].src : defaultImg
}
}, null, false)
},
_browse () {
axios.post(this, '/v1/messageRecord', {'userId': localUser.get('id'), 'targetId': this.$route.params.productId, 'type': 0})
},
_getOrderData () {
axios.get(this, '/v1/package/' + this.$route.params.productId, null,
(ordData) => {
this.orderDataList = ordData
}, null, false)
},
_getCommentData () {
axios.get(this, '/v1/productComment/' + this.$route.params.productId, null,
(comData) => {

this.commentList = comData
}, null, false)
},
_getSystemRecommend () {
axios.get(this, `/v1/recommendProduct`, {
id: this.$route.params.productId,
type: this.$route.params.type,
pageSize: 5
}, (systemRecommendData) => {

this.recommendList = systemRecommendData
})
},
_getCollectionStatus() {
axios.get(this, '/v1/messageRecord/' + localUser.get('id') + '/' + this.$route.params.productId, null,
(res) => {
this.messageRecordId = res.id
this.collectionType = res.collectionType
}, null, false)
},
_get720Pic () {
axios.get(this, '/v1/panorama?productId=' + this.$route.params.productId, null, (detailPicData) => {
if (detailPicData == null || detailPicData.length === 0) {
this.$vux.toast.text('暂时没有720全景图~', 'middle')
} else {
this.detailPicData = detailPicData[0].thumbUrl
if (isIOS()) {
window.webkit.messageHandlers.NativeMethod.postMessage({'methodName': 'showVRPanorama', 'panoramaUrl': this.detailPicData})
} else {
window.android.showVRPanorama(this.detailPicData)
}
}
})
},
collectionToggle () {

if (localUser.get('id') == null) {
this.$vux.toast.text('请先登录！', 'middle')
}
if (this.collectionType === 0) {
this.collectionType = 1
} else {
this.collectionType = 0
}
axios.put(this, '/v1/messageRecord/', {
userId: localUser.get('id'),
targetId: this.$route.params.productId,
collectionType: this.collectionType,
id: this.messageRecordId
}, (res) => {
if (this.collectionType === 0) {
this.$vux.toast.text('取消收藏！')
} else {
this.$vux.toast.text('收藏成功！')
}
})
},
placeOrder (order, startDate, leaveDate) {

const user = localUser.get()
if (user == null) {
this.$vux.toast.text('请先登录！', 'middle')
return
}
const obj = this.orderDataList.find(item => {
return item.id === order.id
})
obj.type = this.$route.params.type
obj.orderTimee = startDate
obj.orderTimeLi = leaveDate
obj.productName = this.productInfo.name
obj.productDesc = this.productInfo.desc
obj.ticketName = order.name

this.$store.commit('SET_SELECT_ORDER', obj)
this.$router.push(`/cart/ProductOrder/${this.$route.params.productId}`)
},
toShare() {
this.showShare = true
},
productLoaded (data) {
},
scroll (position) {
if (position.y <= -100) {
this.opacity = 1
} else {
this.opacity = Math.abs(position.y / 168)
}
},
scrollNum (index) {
this.num = index
if (this.num === 2) {
this.opacity = 1
} else {
this.opacity = 0
}
}
}
}



