import comment from '@/components/comment/comment'
import Scroll from '@/components/scroll/scroll'
import { localUser } from '@/assets/js/local'

export default {
  props: {
    commentDataList: {
      type: Array,
      default: []
    },
    paddingTop: {
      type: Number,
      default: 0
    }
  },
  components: {
    comment,
    Scroll
  },
  // mounted () {
    // this.abc()
  // },
  methods: {
    // abc () {
    //   console.log(this.commentDataList[0].replyList[0])
    // },
    commentAll (item) {
      if (localUser.get('id') == null) {
        this.$vux.toast.text('请先登录', 'middle')
        return
      }
      this.$store.commit('SET_CURRENT_COMMENT', item)
      this.$router.push(`/cart/productCommentDetails`)
    }
  }
}



