
import Previewer from 'vux/src/components/previewer/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import Scroll from '@/components/scroll/scroll'
import Comment from '@/components/comment/comment'
import XDialogg from '@/components/x-dialog/x-dialog'
import { isIOS, isAndroid } from '@/assets/js/brower.js'
import axios from '@/api/axiosApi'
import { localUser } from '@/assets/js/local'

export default {
props: {
productInfo: {
type: Object,
default: {}
},
orderDataList: {
type: Array,
default: []
},
commentDataList: {
type: Array,
default: []
},
recommendList: {
type: Array,
default: []
},
collectionType: {
type: Number,
default: 0
}
},
data () {
return {
showMap: false,
menusMap: {},
showCancel: false,
titleMap: '操作提示',
showGaoDei: false,
showFeatureDialog: false,
showNoticeDialog: false,
orderTime: '',
startDate: '',
leaveDate: '',
options: {
getThumbBoundsFn (index) {
let thumbnail = document.querySelectorAll('.top-wrapper')[0]
let pageYScroll = window.pageYOffset || document.documentElement.scrollTop
let rect = thumbnail.getBoundingClientRect()
return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
}
}
}
},
directives: {
TransferDom
},
components: {
Scroll,
Previewer,
Rater,
Group,
CellBox,
Cell,
Calendar,
Divider,
Comment,
Scroller,
XDialogg,
Datetime,
Confirm,
Actionsheet
},
computed: {
leaveStartDate () {
return this.moment(this.startDate).add(1, 'days').format('YYYY-MM-DD')
}
},
created () {
if (this.$route.params.type === 0) {
this.startDate = this.moment().format('YYYY-MM-DD')
}
},
watch: {
productInfo () {
this.$refs.scroll.scrollTo(0, 0)
},
recommendList () {
this.$refs.scroll.$el.style.height = window.innerHeight + 'px'
this.$refs.scrollRecom.style.width = (200 + 10) * this.recommendList.length + 'px'


}
},
methods: {
back () {
this.$router.go(-1)
},
scroll (position) {
this.$emit('scroll', position)
},
previewImage () {
this.$refs.previewer.show(0)
},
toggleFeatureDialog () {
this.showFeatureDialog = !this.showFeatureDialog
},
toggleNoticeDialog () {
this.showNoticeDialog = !this.showNoticeDialog
},
allComment () {
this.$emit('allComment')
},
selectOrder(order) {
if (this.$route.params.type === '1' && this.startDate === '') {
this.$vux.toast.text('请选择出行日期', 'middle')
return
}
if (this.$route.params.type === 0 && ((this.startDate === '' || null) || (this.leaveDate === '' || null))) {
this.$vux.toast.text('请选择入住日期时间段', 'middle')
return
}
this.$emit('productPlaceOrder', order, this.startDate, this.leaveDate)
},
collectionToggle () {
this.$emit('collectionToggle')
},
clickMore(item) {
this.$router.replace(`/cart/product/${item.id}/${this.$route.params.type}`)
},
detailsService () {
this.$router.push('/cart/DetailServiceMap/' + this.$route.params.productId)
},
detailsPic () {
this.$emit('showPanorama')
},
Trailer () {
if (this.productInfo.publicityId == null) {
this.$vux.toast.text('暂时没有视频资料~', 'middle')
} else {
axios.get(this, `/v1/contentDesc/${this.productInfo.publicityId}`, null, (data) => {
this.$router.push('/content/' + data.id + '/景区宣传片')
})
}
},
guide () {
this.$vux.toast.text('缺手绘地图~', 'middle')
},
index () {
this.$vux.toast.text('没有数据~', 'middle')
},
addressCellMap () {
if (isIOS()) {

window.webkit.messageHandlers.NativeMethod.postMessage({'methodName': 'openMapNavi', 'latitude': this.productInfo.latitude, 'longitude': this.productInfo.longitude})
} else if (isAndroid()) {
this.menusMap = JSON.parse(window.android.isInstallByread())
if (this.menusMap == null || Object.getOwnPropertyNames(this.menusMap).length === 1) {
this.showCancel = false
this.showGaoDei = true
return
}
if (this.menusMap.baiDu === '2') {
this.menusMap.baiDu = '百度地图'
}
if (this.menusMap.gaoDei === '1') {
this.menusMap.gaoDei = '高德地图'
}
if (this.menusMap.tenXun === '3') {
this.menusMap.tenXun = '腾讯地图'
}
this.showMap = true
}
},
onConfirmGaoDei () {
if (isAndroid()) {
window.android.makeUpMap(this.productInfo.latitude, this.productInfo.longitude, '', 0, 1, '')
}
},
handleClickMenu (value) {
if (isAndroid()) {
if (value === 'baiDu') {
window.android.makeUpMap(this.productInfo.latitude, this.productInfo.longitude, '', 2, 1, '')
} else if (value === 'gaoDei') {
window.android.makeUpMap(this.productInfo.latitude, this.productInfo.longitude, '', 1, 1, '')
} else if (value === 'tenXun') {
window.android.makeUpMap(this.productInfo.latitude, this.productInfo.longitude, '', 3, 1, '')
}
}
},
toAI () {
this.$router.push('/chatAI')
},
commentAll (commentDataList) {
if (localUser.get('id') == null) {
this.$vux.toast.text('请先登录', 'middle')
return
}
this.$store.commit('SET_CURRENT_COMMENT', this.commentDataList)
this.$router.push('/cart/productCommentDetails')
}
}
}



